</template>

<script>
import bottomNavigation from '../components/BottomNavigation'

export default {
  components: {
    'custom-bottom-nav': bottomNavigation
  },
  name: 'HelloWorld',
  data () {
    return {
      msg: '   '
    }
  }
}
</script>

<style scoped>
.wrapper {
  font-weight: normal;
  background-color: #fff;
  padding: 16px;
  height: 1000px;
}
.sign-in-title {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 16px;
}
.sign-in-desc {
  font-size: 14px;
  line-height: 24px;
}
</style>



